nav{
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background:linear-gradient(45deg, rgb(156,14,145), midnightblue);
}
.liste{
    list-style: none;
    background:linear-gradient(45deg, rgb(156,14,145), midnightblue);
    height: 100%;
    display: flex;
    justify-content:center;
    align-items: center;
    position: relative;
    font-size: 22px;
}
.liste .items a {
    margin: 0 15px;
    color: aliceblue;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
}
.btn{
    display: none;
    position: absolute;
    right: 10px;
    top: 15px;
    color: black;
    font-size: 18px;
}

@media screen and (max-width: 500px){
    .liste{
        flex-direction: column;
        height: auto;
    }
    .items:nth-child(1){
        border-top: 1px solid rgba(255, 255, 255, 0.6);
        margin-top: 50px;
    }
    .items{
        height: 70px;
        width: 100%;
        border-bottom: 1px solid rgba(255, 255, 255, 0.6);
        line-height: 70px;
        text-align: center;
    }
    .btn{
        display: block;
    }
    
}