.title-form{
    text-align: center;
    font-size: 55px;
    margin: 70px 0 0;
}

.conainer-form{
    padding: 10px;
    max-width: 900px;
    margin: 30px auto 0;
    display: flex;
    flex-direction: column;
}

.conainer-form label{
    font-size: 20px;
}

.conainer-form input, .conainer-form textarea{
    padding: 10px;
    margin-bottom: 25px;
    font-size: 18px;
}

.conainer-form textarea{
    min-height: 200px;
    font-family: Arial, Helvetica, sans-serif;
}
.conainer-form button{
    padding: 10px 15px;
    width: 200px;
    background: transparent;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: rgb(49, 124, 235);
    color: aliceblue;
}