.article-containt{
    max-width: 700px;
    min-width: 200px;
    margin: 100px auto;
    padding: 0 20px 0 20px;
}

.article-containt h2{
    font-size: 45px;
    margin-bottom: 20px;
}

.article-containt p {
    font-size: 25px;
}

.article-containt a{
    display: inline-block;
    width: 20%;
    margin: 10px;
    padding: 5px;
    border: none;
    background-color: rgb(15, 161, 24);
    border-radius: 6px;
    color: aliceblue;
    text-decoration: none;
    text-align: center;
}